@import url(https://fonts.googleapis.com/css2?family=Space+Mono&display=swap);

.footerWrapper {
    position:absolute;
    top:0;
    width:100%;
    color:rgb(65, 65, 65);
    font-size:1.5rem;
    padding:10px 0;
}
.footerWrapper a {
    text-decoration:none;
    color:rgb(65, 65, 65);
}
.footerWrapper a:hover {
    color:red;
}
.form {
    width:750px;
    background-color:black;
    background-image:
    radial-gradient(white, rgba(255,255,255,.2) 2px, transparent 40px),
    radial-gradient(white, rgba(255,255,255,.15) 1px, transparent 30px),
    radial-gradient(white, rgba(255,255,255,.1) 2px, transparent 40px),
    radial-gradient(rgba(255,255,255,.4), rgba(255,255,255,.1) 2px, transparent 30px);
    background-size: 550px 550px, 350px 350px, 250px 250px, 150px 150px;
    background-position: 0 0, 40px 60px, 130px 270px, 70px 100px;
}
.buttonBackground {
    background-color: #3dc8b7;
}
.LeaderWrapper {
    text-align:center;
}
.LeaderWrapper p {
    font-size:2.0rem;
    color:white;
}
.LeaderWrapper h3 {
    font-size:6.0rem;
    padding:10px 0;
    color:white;
}
@media(max-width: 800px){
    .LeaderWrapper h3 {
        font-size:3.0rem;
    }
    .LeaderWrapper p {
        font-size:1.5rem;
    }
}
.demographicsWrapper img {
    padding-top:250px;
    width:500px;
    height:auto;
    position:relative;
}
.boundingNumber {
    opacity:0;
    display:flex;
    align-items:center;
}
.boundingBox:hover p,
.boundingBox:focus p{
    opacity:1;
    color:red;
    background:black;
    font-size:3rem;
}
.boundingBox {
    position: absolute;
    box-shadow: 0 0 0 3px #11fd00 inset;
    display:flex;
    flex-wrap:wrap;
    justify-content:center; 
    cursor:pointer;
}
.demographicGrid {
    display:grid;
    align-items: center;  
    grid-template-columns: minmax(0, 1fr) minmax(0, 1fr) minmax(0, 1fr) minmax(0, 1fr);
    grid-gap:10px;
}
.demographicResults {
    padding:5px;
    text-align:center;
}
.demographicResults p {
    font-size: 1.5rem;
    color:white;
}
.style0 {
    border:3px solid #11fd00;
}
.style1 {
    border:3px solid red;
}
.styleBox1 {
    box-shadow: 0 0 0 3px red inset;
}
.style2 {
    border:3px solid blue;
}
.styleBox2 {
    box-shadow: 0 0 0 3px blue inset;
    }
.style3 {
    border:3px solid yellow;
}
.styleBox3 {
    box-shadow: 0 0 0 3px yellow inset;   
}
.style4 {
    border:3px solid purple;
}
.styleBox4 {
    box-shadow: 0 0 0 3px purple inset;   
}
.style5 {
    border:3px solid orangered;
}
.styleBox5 {
    box-shadow: 0 0 0 3px orangered inset;   
}
.style6 {
    border:3px solid turquoise;
}
.styleBox6 {
    box-shadow: 0 0 0 3px turquoise inset;   
}
.style7 {
    border:3px solid pink;
}
.styleBox7 {
    box-shadow: 0 0 0 3px pink inset;   
}
legend {
    font-size:30.0rem;
}

